export default "/*\n    Name: StarWarsIntro.css\n    URI: polarnotion.github.io/starwarsintro\n    Author: Polar Notion\n    Author URI: http://polarnotion.com/\n    Description: A simple CSS library for creating a Star Wars Intro Crawl. May the Force be with you.\n    Version: 1.0\n*/\n.wrapper {\n  position: absolute;\n  z-index: 100;\n  width: calc(100vw - 10%);\n  height: 100vh;\n}\n.title {\n  transform: scaleY(3);\n  display: inline-block;\n  margin-top: 100px;\n}\n.star-wars-intro {\n  width: 100%;\n  height: 100%;\n  font-family: \"Droid Sans\", arial, verdana, sans-serif;\n  font-weight: 700;\n  color: #face51;\n  background-color: #000;\n  overflow: hidden;\n  position: relative;\n}\n.star-wars-intro p.intro-text {\n  position: relative;\n  max-width: 16em;\n  font-size: 200%;\n  font-weight: 400;\n  margin: 20% auto;\n  color: #4ee;\n  opacity: 0;\n  z-index: 1;\n  text-align: left;\n  -webkit-animation: intro 3.5s ease-out;\n  animation: intro 3.5s ease-out;\n}\n.star-wars-intro .main-content {\n  margin-left: auto;\n  margin-right: auto;\n  position: absolute;\n  z-index: 3;\n  width: 98%;\n  height: 50em;\n  bottom: 0;\n  font-size: 80px;\n  font-weight: bold;\n  text-align: justify;\n  overflow: hidden;\n  transform-origin: 50% 100%;\n  transform: perspective(350px) rotateX(25deg);\n}\n@media screen and (max-width: 720px) {\n  .star-wars-intro .main-content {\n    font-size: 35px;\n  }\n}\n.star-wars-intro .main-content:after {\n  position: absolute;\n  content: \" \";\n  top: 0;\n  bottom: 60%;\n  background-image: linear-gradient(to top, black 0%, transparent 100%);\n  pointer-events: none;\n}\n.star-wars-intro .title-content {\n  position: absolute;\n  top: 100%;\n  -webkit-animation: scroll 150s linear 4s forwards;\n          animation: scroll 150s linear 4s forwards;\n}\n@media screen and (max-width: 720px) {\n  .star-wars-intro .title-content {\n    position: absolute;\n    top: 100%;\n    -webkit-animation: scroll 100s linear 4s forwards;\n    